import React, { useEffect, useState } from "react";
import { Card, Dropdown } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { DataTable, PageHeader, actionFormatter } from "../../../components";
import {
  checkValidData,
  decodeQueryData,
  getSortType,
  logger,
} from "../../../utils";
import { locationServices } from "../../../services";

function Locations() {
  const location = useLocation();
  const { search } = location;
  const [locationData, setLocationData] = useState([]);
  const [param, setParam] = useState({});
  const [page, setPage] = useState(1);
  const [sizePerPage, setSizePerView] = useState(10);
  const [searchName, setSearchName] = useState("");
  const [defaultSort, setDefaultSort] = useState([
    {
      dataField: "",
      order: "",
    },
  ]);
  const [tableLoader, setTableLoader] = useState(false);
  const [noOfPage, setNoOfPage] = useState();
  const [totalCount, setTotalCount] = useState(0);
  // const [show, setShow] = useState(false);
  // const [rowData, setRowData] = useState("");
  // const [loading, setLoading] = useState(false);
  //   const [searchName, setSearchName] = useState("");

  useEffect(() => {
    if (search) {
      const data = decodeQueryData(search);
      setParam(data);
      setPage(data?.page ?? 1);
      if (data?.sortType) {
        const sortData = [
          {
            order: getSortType(data?.sortType),
            dataField: data?.sortBy,
          },
        ];
        setDefaultSort(sortData);
      } else {
        setDefaultSort({
          dataField: "",
          order: "",
        });
      }
    }
  }, [location]);

  // const onHandleShow = (row) => {
  //   setShow(true);
  //   setRowData(row);
  // };

  const getLocationData = async () => {
    setTableLoader(true);
    try {
      let queryParams = {
        offset: (page - 1) * sizePerPage,
        limit: sizePerPage,
        sortBy: param?.sortBy,
        sortType: param?.sortType,
        name: searchName,
      };
      const res = await locationServices.getLocationList(queryParams);
      const { success, data } = res;
      if (success) {
        setLocationData(data.rows);
        setNoOfPage(data.count > 0 ? Math.ceil(data.count / sizePerPage) : 1);
        setTotalCount(data.count);
      }
    } catch (error) {
      logger(error);
    }
    setTableLoader(false);
  };

  //   const headerSortingClasses = (column, sortOrder) => {
  //     return sortOrder === "asc" ? "sorting_asc" : "sorting_desc";
  //   };

  //   const onSortColumn = (field, order) => {
  //     const data = { ...param };
  //     data.sortBy = field;
  //     data.sortType = order === "asc" ? "ASC" : "DESC";
  //     navigateWithParam(data, navigate, pathname);
  //     tableReset();
  //   };

  const options = () => {
    const optionsArr = [
      {
        name: "Edit",
        icon: "icon ni ni-pen",
        path: "#addForm",
        action: "confirm",
        onClickHandle: () => {
          // onHandleShow(row);
        },
      },
    ];

    return optionsArr;
  };
  const columns = [
    {
      dataField: "id",
      text: "Location Id",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "thumbnailUrl",
      text: "Region",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) => checkValidData(row?.region?.name),
    },
    {
      dataField: "area",
      text: "Area",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "Hub Name",
      text: "Hub Name",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },

      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "action",
      text: "Hub Mapped Count",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },

      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "action",
      text: "Action",
      headerClasses: "d-flex justify-content-end",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },

      formatter: (cell, row) => actionFormatter(options(row)),
    },
  ];

  const tableReset = () => {
    setTableLoader(true);
    setLocationData([]);
    setNoOfPage(0);
    setTotalCount(0);
  };

  const getSearchValue = (val) => {
    setSearchName(val);
    if (val) {
      tableReset();
    }
  };

  useEffect(() => {
    getLocationData();
  }, [param]);
  return (
    <div className="nk-block-head nk-block-head-sm locationPage">
      <div className="nk-block-between">
        <Card className="w-100 rounded-2 mb-4">
          <Card.Body>
            <div className="d-flex justify-content-between">
              <PageHeader heading="Locations" />
              <Dropdown>
                <Dropdown.Toggle variant="primary" id="dropdown-basic">
                  Export As
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
                  <Dropdown.Item href="#/action-2">
                    Another action
                  </Dropdown.Item>
                  <Dropdown.Item href="#/action-3">
                    Something else
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            </div>
          </Card.Body>
        </Card>
      </div>
      <Card>
        <Card.Body className="p-0">
          <DataTable
            sizePerPagination={false}
            hasLimit
            noOfPage={noOfPage}
            sizePerPage={sizePerPage}
            page={page}
            count={totalCount}
            tableData={locationData}
            tableColumns={columns}
            // param={param}
            defaultSort={defaultSort}
            tableLoader={tableLoader}
            setSizePerPage={setSizePerView}
            tableReset={tableReset}
            getSearchValue={getSearchValue}
            // searchPlaceholder="Brand Name"
          />
        </Card.Body>
      </Card>
    </div>
  );
}

export default Locations;
